.basic {
  margin-top: -110px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.ul-list {
   position: relative;
   display: flex;
   transform: rotate(-25deg) skew(25deg);
   transform-style: preserve-3d;
}
.ul-list li {
  position: relative;
  list-style: none;
  width: 60px;
  height: 60px;
  margin: 0px 20px;
}
.ul-list li:before{
  content: '';
  position: absolute;
  bottom: -10px;
  left: -5px;
  width: 100%;
  height: 10px;
  background: #303030;
  transform-origin: top;
  transform: skewX(-41deg);
}
.ul-list li:after{
  content: '';
  position: absolute;
  top:5px;
  left: -9px;
  width: 9px;
  height: 100%;
  background: #303030;
  transform-origin: right;
  transform: skewY(-49deg);
}
.ul-list li span{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex !important;
  background: #303030;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 30px !important;
  transition: 1s ease-out;
}
.ul-list li:hover span {
  z-index: 1000;
  transition: .3s;
  color: #fff;
  box-shadow: -1px 1px 1px rgba(0, 0, 0, .5);
}
.ul-list li:hover span:nth-child(3){
  transform: translate(20px, -20px);
  opacity: .6;
}
.ul-list li:hover span:nth-child(2){
  transform: translate(10px, -10px);
  opacity: .4;
}
.ul-list li:hover span:nth-child(1){
  transform: translate(0px, 0px);
  opacity: .2;
}
.ul-list li:nth-child(1):hover span{
  background: #52E19F !important;
}
.ul-list li:nth-child(2):hover span{
  background: #2C3456 !important;
}
.ul-list li:nth-child(3):hover span{
  background: #EA6E96 !important;
}


